import React, { Component } from "react";
import "./AddTask.css";

class AddTask extends Component {
  minDate = new Date().toISOString().slice(0, 10);
  state = {
    text: "",
    checked: false,
    date: this.minDate
  };

  handleText = e => {
    this.setState({
      text: e.target.value
    });
  };

  handleCheckbox = e => {
    this.setState({
      checked: e.target.checked
    });
  };

  handleDate = e => {
    this.setState({
      date: e.target.value
    });
  };

  //czyszczenie formularza text, obsługa dodawania zadania
  handleClick = () => {
    // console.log("dodaj task");
    const { text, checked, date } = this.state;
    if (text.length > 2) {
      const add = this.props.add(text, date, checked);
      if (add) {
        this.setState({
          text: "",
          checked: false,
          date: this.minDate
        });
      }
    } else {
      console.log("Za krótka nazwa");
    }
  };

  render() {
    //ustalenie maxDate, zmiana na date BEZ stringa
    let maxDate = this.minDate.slice(0, 4) * 1 + 1;
    maxDate = maxDate + "-12-31";
    return (
      <>
        <div className="form">
          <input
            type="text"
            placeholder="dodaj zadanie"
            value={this.state.text}
            onChange={this.handleText}
          />

          <input
            className="important"
            type="checkbox"
            checked={this.state.checked}
            id="important"
            onChange={this.handleCheckbox}
          />
          <div className="importantLabel">
            <label htmlFor="important">zadanie ważne</label>
          </div>
          <hr />
          <div className="finishDateLabel">
            <label htmlFor="date">Termin wykonania: </label>
          </div>
          <input
            type="date"
            value={this.state.date}
            min={this.minDate}
            max={maxDate}
            onChange={this.handleDate}
          />

          <button onClick={this.handleClick}>Dodaj zadanie</button>
          <hr />
        </div>
      </>
    );
  }
}

export default AddTask;
