import React from "react";
import Task from "./Task";

const TaskList = props => {
  const active = props.tasks.filter(task => task.active);
  const done = props.tasks.filter(task => !task.active);

  // sortowanie zadań zrobionych wg. daty ukończenia

  // done.sort((a, b) => {
  //   return b.finishDate - a.finishDate;
  // });

  if (done.length >= 2) {
    done.sort((a, b) => {
      // zmiana wielkości liter do porównania
      a = a.text.toLowerCase();
      b = b.text.toLowerCase();

      if (a.finishDate < b.finishDate) {
        return 1;
      }
      if (a.finishDate > b.finishDate) {
        return -1;
      }
      return 0;
    });
  }

  // sortowanie alfabetyczne zadań do wykonania

  if (active.length >= 2) {
    active.sort((a, b) => {
      if (a.text < b.text) return -1;
      if (a.text > b.text) return 1;
      return 0;
    });
  }

  const activeTasks = active.map(task => (
    <Task
      key={task.id}
      task={task}
      delete={props.delete}
      change={props.change}
    />
  ));
  const doneTasks = done.map(task => (
    <Task
      key={task.id}
      task={task}
      delete={props.delete}
      change={props.change}
    />
  ));

  return (
    <React.Fragment>
      <div className="active">
        <h1>Zadania do zrobienia</h1>
        {activeTasks.length > 0 ? (
          activeTasks
        ) : (
          <p>Wszystkie zadania zostały wykonane, masz wolne</p>
        )}
      </div>
      <hr />
      <div className="done">
        <h3>
          Zadania zrobione <em>({done.length})</em>
        </h3>
        {done.length > 5 && (
          <span style={{ fontSize: 10 }}>
            Wyświetlane jest jedynie 5 ostatnich zadań
          </span>
        )}
        {doneTasks.slice(0, 5)}
      </div>
    </React.Fragment>
  );
};

export default TaskList;
