import React from "react";

const Task = props => {
  const style = {
    backgroundColor: "red",
    textTransform: "uppercase"
  };

  const { text, date, id, active, important, finishDate } = props.task;

  if (active) {
    return (
      <>
        <div className="taskStyle">
          <strong style={important ? style : null}>{text}</strong>
          <em> - termin do : </em>
          <span>{date} </span>
          <div className="taskButton">
            <button onClick={() => props.change(id)}>Zostało wykonane</button>
          </div>
          <div className="deleteButton">
            <button onClick={() => props.delete(id)}>Usuń</button>
          </div>
        </div>
      </>
    );
  } else {
    // nowa data, oznaczająca termin wykonania zadania
    const finish = new Date(finishDate).toLocaleString();

    return (
      <div>
        <div className="taskStyle">
          <strong>{text}</strong>
          <em>, termin wykonania do: {date} </em>
          <br />
          Potwierdzenie wykonania : <span>{finish} </span>
          <div className="deleteButton">
            <button onClick={() => props.delete(id)}>usuń</button>
          </div>
        </div>
      </div>
    );
  }
};

export default Task;
