import React, { Component } from "react";
import AddTask from "./AddTask";
import TaskList from "./TaskList";

import "./App.css";

class App extends Component {
  // licznik id
  counter = 6;
  state = {
    tasks: [
      {
        id: 0,
        text: "Zakupy",
        date: "2019-03-13",
        important: true,
        active: true,
        finishDate: null
      },
      {
        id: 1,
        text: "Szkolenie z rozwoju osobistego",
        date: "2019-03-10",
        important: true,
        active: true,
        finishDate: null
      },
      {
        id: 2,
        text: "Kurs React",
        date: "2019-03-30",
        important: false,
        active: true,
        finishDate: null
      },
      {
        id: 3,
        text: "Wyjazd służbowy",
        date: "2019-03-08",
        important: true,
        active: true,
        finishDate: null
      },
      {
        id: 4,
        text: "Mecz siatkówki",
        date: "2019-02-19",
        important: false,
        active: true,
        finishDate: null
      },
      {
        id: 5,
        text: "Szkolenie Kraków",
        date: "2019-01-10",
        important: false,
        active: true,
        finishDate: null
      }
    ]
  };

  //usuwanie elementów z tablicy
  deleteTask = id => {
    // wyszukanie elementu przez findindex i usunięcie przez metodę splice

    // const tasks = [...this.state.tasks];
    // const index = tasks.findIndex(task => task.id === id);
    // tasks.splice(index, 1);
    // this.setState({
    //   tasks
    // });

    //usunięcie i wybranie elementu przez metodę filter !
    let tasks = [...this.state.tasks];
    tasks = tasks.filter(task => task.id !== id);
    this.setState({
      tasks
    });
  };

  //zmiana statusu elementu
  changeTaskStatus = id => {
    console.log("change w stanie elementu o id" + id);
    const tasks = Array.from(this.state.tasks);
    tasks.forEach(task => {
      if (task.id === id) {
        task.active = false;
        task.finishDate = new Date().getTime();
      }
    });
    this.setState({
      tasks
    });
  };
  // dodwawanie nowych zadań, utworzenie nowego obiektu
  addTask = (text, date, important) => {
    // console.log("dodany obiekt");
    const task = {
      id: this.counter,
      text, // text z inputa
      date, // text z inputa
      important, // wartość z inputa
      active: true,
      finishDate: null
    };
    this.counter++;
    // console.log(task, this.counter);
    this.setState(prevState => ({
      tasks: [...prevState.tasks, task]
    }));
    return true;
  };
  render() {
    return (
      <React.Fragment>
        <div className="App">
          <h1>Twoja lista zadań do zrobienia</h1>
        </div>
        <AddTask add={this.addTask} />
        <TaskList
          tasks={this.state.tasks}
          delete={this.deleteTask}
          change={this.changeTaskStatus}
        />
      </React.Fragment>
    );
  }
}

export default App;
